import React from 'react';
import Features from './Features';
import './sass/main.scss';

const Main = () => {
  return (
    <main>
      <h2 className={'main__title'}>Why JOBLIO<span>?</span></h2>
      <Features />
      <h2 className={'main__title'}>Who <span>We </span>Are?</h2>
      <section className={'section-who'}>
        <div className={'text-block'}>
          <h3>Who We Are</h3>
          <p>We are a technology company that is reinventing the process
             of global recruitment of personnel.</p>
          <p>Our technology combined with our professional understanding of 
            international migration laws and deep knowledge of the global recruitment 
            landscape allows employers to efficiently and transparently hire staff 
            from other countries. Joblio directly connects employers and employees 
            in circumvention of middlemen and optimizes efficiency of the hiring, 
            processing and relocation of foreign staff. Joblio conducts KYC/KYB while 
            bringing efficiency into the ecosystem and ensuring that transparency and 
            human rights are protected. Joblio protects the employer/employee ecosystem 
            by maintaining transparency in a proprietary way.</p>
        </div>
        <div className={'pic-block'}></div>

      </section>
    </main>
  )
}

export default Main;